<template>
  <div>
    <h1>Report della conservazione</h1>
    <hr />
    <div class="row">
      <div class="col-lg-12">
        <table class="table">
          <thead>
            <th>origine / stato versamento</th>
            <th>da conservare</th>
            <th>versato</th>
            <th>scartato</th>
            <th>conservato</th>
            <th>conservato pdv</th>
            <th>totale conservato</th>
            <th>conservato %</th>
            <th>op</th>
          </thead>
          <tbody>
            <tr v-for="(item, index) in list" :key="index">
              <td>{{ changeAdminName(item.origin_id) }}</td>
              <td>{{ item.da_v }}</td>
              <td>{{ item.ver }}</td>
              <td>{{ item.error }}</td>
              <td>{{ item.cons }}</td>
              <td>{{ item.pdv }}</td>
              <td>{{ getTotal(item) }}</td>
              <td>{{ getPercent(item) }}</td>
              <td>
                <a 
                  @click="update(item.origin_id)"
                >
                <i
                  class="fa fa-refresh"
                  aria-hidden="true"
                ></i>
                </a>
              </td>
            </tr>
          </tbody>
          <tfoot>
            <th>origine / stato versamento</th>
            <th>da conservare</th>
            <th>versato</th>
            <th>scartato</th>
            <th>conservato</th>
            <th>conservato pdv</th>
            <th>totale conservato</th>
            <th>conservato %</th>
            <th>op</th>
          </tfoot>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      adminList: [],
    };
  },
  mounted() {
    this.getList();
    this.getAdmin();
  },
  methods: {
    getList() {
      // var statusList = [0, 1, 2, 6, 7, 12, "null"];
      // var total = [];
      this.$store.dispatch("loading");
      this.axios.get(this.apiURL + "/retention/report2").then((r) => {
        this.list = r.data;
        //   console.log(JSON.stringify(element));
        this.list.push(this.getAllTot(r.data));
        // this.list.forEach((element) => {
        //   element.forEach((item) => {
        //     total[item.status]+= item.quantity
        //   });
        // });
        // console.log(JSON.stringify(total));
        // list.push({ "total": [] });
        // console.log(JSON.stringify(this.list));
        this.$store.dispatch("loaded");
      });
    },
    getAdmin() {
      this.$store.dispatch("loading");
      this.axios.get(this.apiURL + "/retention/admin").then((r) => {
        this.adminList = r.data;
        // console.log(JSON.stringify(r.data));

        this.$store.dispatch("loaded");
      });
      // console.log();
    },
    changeAdminName(i) {
      // console.log( typeof i);
      var origin = "";
      this.adminList.forEach((element) => {
        // console.log(element.id+" - "+i + " => "+typeof element.id + " = "+ typeof parseInt(i));
        // console.log( element.id+" - "+element.name);
        if (element.id === parseInt(i)) {
          // console.log(element.id + " => " + element.name);
          origin = i + " - " + element.name;
          // exit();
        }
      });
      return origin == "" ? i : origin;
    },
    getTotal(item) {
      var conservato = 0;
      var tot = 0;
      tot =
        parseInt(item.da_v) +
        parseInt(item.ver) +
        parseInt(item.error) +
        parseInt(item.cons) +
        parseInt(item.pdv);
      conservato = parseInt(item.cons) + parseInt(item.pdv);
      return conservato + "/" + tot;
    },
    getPercent(item) {
      var conservato = 0;
      var tot = 0;
      tot =
        parseInt(item.da_v) +
        parseInt(item.ver) +
        parseInt(item.error) +
        parseInt(item.cons) +
        parseInt(item.pdv);
      conservato = parseInt(item.cons) + parseInt(item.pdv);
      if (conservato == 0 && tot == 0) {
        return "0.00%";
      }
      return ((conservato / tot) * 100).toFixed(2) + "%";
    },
    getAllTot(item) {
      var rows = { da_v: 0, ver: 0, error: 0, cons: 0, pdv: 0 };

      item.forEach((element) => {
        element.forEach((item) => {
          rows.da_v += item.da_v;
          rows.ver += item.ver;
          rows.error += item.error;
          rows.cons += item.cons;
          rows.pdv += item.pdv;
        });
      });
    },
    update(id) {
      window.open(
        "https://archicons.archivium-srl.it/ArchiHub/procedures/varie/retention/report.php?origin_id=" +
          id, "_blank"
      );
    },
  },
};
</script>